$(document).ready(function(){

    console.log('hello');

    // swiper_full - wide basic
    var swiper_full = new Swiper('.full', {
        pagination: {
        el: '.swiper-pagination',
        clickable: true,
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        mousewheel: true,
        loop:true
    });

    // swiper_fullparallax
    var swiper_full = new Swiper('.parallax', {
        parallax:true,
        speed:900,
        spaceBetween: 0,
        pagination: {
        el: '.swiper-pagination',
        clickable: true,
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        mousewheel: true,
        loop:true
    });

    swiper_full.on('slideChangeTransitionStart', function(){
        // console.log(swiper_full.activeIndex);
        var activeIdx = swiper_full.activeIndex;
        var activeSldbg = $('.swiper-slide').eq(activeIdx).find('.parallbg');
        TweenMax.from( activeSldbg, 1.5, { scale:1.5 } );

    });

    // swiper_group
    var swiper_group = new Swiper('.group', {
        slidesPerView: 1.5,
        spaceBetween: 20,
        centeredSlides: true,
        // freeMode: true,
        pagination: {
        el: '.swiper-pagination',
        clickable: true,
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        loop:true
    });
    
    // swiper_freemode
    var swiper_freemode = new Swiper('.freemode', {
        slidesPerView: 2.5,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
        el: '.swiper-pagination',
        clickable: true,
        },
        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
        loop:true
    });    

    // swiper_parallax


});